.navigation {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;

  .nav-links-container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-family: "Indie Flower", cursive;
    font-size: 1em;
    margin-right: 15px;

    .pending {
      padding: 10px 15px;
      color: gray;
      cursor: pointer;
      text-decoration: none;
    }

    .active {
      padding: 10px 15px;
      color: black;
      cursor: pointer;
      text-decoration: none;
    }
  }

  .logo-container {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    margin-left: 15px;

    .site-logo {
      font-family: "Indie Flower", cursive;
      text-decoration: none;
      font-size: 2.5em;
      color: black;
    }
  }
}
