html, body, #root{
    margin: 0;
    height: 100%;
    width: 100%;
}

.opening-banner {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-image: url("../../assets/background.jpg");
  background-position: center center;
  background-size: cover;

  .text {
    display: flex;
    width: 80%;
    height: 50%;
    flex-direction: column;
    background-color: white;
    justify-content: center;
    align-items: center;
  }
}
